import React, {Component} from 'react';
import {withAuth} from "./darkMode";
import {Link, Redirect} from 'react-router-dom'

 export default withAuth(({isAuthorized, authorize, darkMode, changeTheme}) => {

    return (
      isAuthorized ?
        <Redirect to='/'/>
        :
        <div className={`${darkMode ? 'darkBody App' : 'App'}`}>
          <div className={darkMode ? 'darkBody' : ''}>
            <div className={`App container ${darkMode ? 'darkBodySite' : ''}`}>
              <nav
                className={`navbar sticky-top navbar-light bg-light header red margin ${darkMode ? 'darkHeader' : ''}`}>
                <Link to="/">
                  <button className={darkMode ? 'btn btn-secondary' : 'btn btn-warning'}>
                    <i className="fas fa-arrow-left"/>
                  </button>
                </Link>

                <button className={darkMode ? 'btn btn-secondary' : 'btn btn-warning'} onClick={() => {
                  changeTheme()
                }}>
                  {darkMode ? <i className="fas fa-sun"/> : <i className="fas fa-moon"/>}
                </button>
              </nav>
              <div className={darkMode ? 'darkTableMovie login' : 'login'}>
                <h1 className={'loginText'}>Log in to open</h1>
                <button onClick={authorize} className={'btn btn-danger logBtn'}>Log in</button>
              </div>
            </div>
          </div>
        </div>
    );

})

