import React, {Component} from 'react';
import {Link} from "react-router-dom";
import {ThemeContext} from "./darkMode";
import {AuthContext} from "./darkMode";

class User extends Component {

  state={
    history:true
  };

  deleteHistory = () =>{
    localStorage.removeItem('history');
    this.setState({history:false});
};

  render() {

    return (
      <AuthContext.Consumer>
        {auth => (
          <ThemeContext.Consumer>
            {theme => (
              <div className={theme.darkMode ? 'darkBody' : ''}>
                <div className={`App container ${theme.darkMode ? 'darkBodySite' : ''}`}>
                  <nav
                    className={`navbar sticky-top navbar-light bg-light header red margin ${theme.darkMode ? 'darkHeader' : ''}`}>
                    <Link to="/">
                      <button className={theme.darkMode ? 'btn btn-secondary' : 'btn btn-warning'}>
                        <i className="fas fa-arrow-left"/>
                      </button>
                    </Link>
                    <div>
                      <button className={theme.darkMode ? 'btn btn-secondary' : 'btn btn-warning'}
                              onClick={this.deleteHistory}>
                        <i className="fas fa-trash"/>
                      </button>
                      <button className={theme.darkMode ? 'btn btn-secondary' : 'btn btn-warning'} onClick={() => {
                        theme.changeTheme()
                      }}>
                        {theme.darkMode ? <i className="fas fa-sun"/> : <i className="fas fa-moon"/>}
                      </button>
                      <button className={theme.darkMode ? 'btn btn-secondary' : 'btn btn-warning'}
                              onClick={auth.logOut}>Log out
                      </button>

                    </div>
                  </nav>
                  <div className='d-flex flex-wrap justify-content-start'>
                    {JSON.parse(localStorage.getItem('history'))&&this.state.history
                      ?
                      JSON.parse(localStorage.getItem('history')).map((recommend) => {
                        return (
                          <div className={'marginAuto'} key={recommend.id}>

                            <div
                              className={`movieTableRecommends movieTablePopSmall marginAuto ${theme.darkMode ? 'darkTableMovie colorText' : ''}`}>
                              <Link className={'colorText'} to={{
                                pathname: `/MoviePage/${recommend.id}`,
                                state: {
                                  movie_id: recommend.id
                                }
                              }}>
                                <img className={'recommendImg posterSmall'}
                                     src={`https://image.tmdb.org/t/p/w500${recommend.poster_path}`}
                                     alt={recommend.title}/>
                              </Link>
                              <h6>{recommend.title}</h6>
                            </div>

                          </div>
                        );
                      })
                      :
                      <div>
                        <h2>Your story is empty</h2>
                      </div>}
                  </div>
                </div>
              </div>

            )
            }

          </ThemeContext.Consumer>
        )}
      </AuthContext.Consumer>


    );
  }
}

export default User;