import React, {Component, createContext, useState} from 'react';
import auth0 from 'auth0-js';
import {withRouter} from 'react-router-dom';

export const ThemeContext = createContext();
export const AuthContext = createContext({isAuthorized:false});


class Context extends Component{
  state={
    darkMode:false,
    isAuthorized:false
  };

  auth0 = new auth0.WebAuth({
    domain:"dev-adkw966q.eu.auth0.com",
    clientID:"2MIFRHkLZzEz5Pje5a8U99UboUia2MxK",
    redirectUri:"http://localhost:3000/callback",
    responseType:"token id_token",
    scope: "openid"

  });

  changeTheme=()=>{

     this.setState({darkMode:!this.state.darkMode},
       ()=>{
       localStorage.setItem('dark',this.state.darkMode);
       console.log(this.state.darkMode);});


  };


  componentDidMount() {
    console.log(localStorage.getItem('dark'));

    if(window.localStorage.length){
      this.setState({darkMode:JSON.parse(localStorage.getItem('dark'))}, ()=>(console.log(this.state.darkMode)));
      console.log('Засетано')
    }
    this.setState({isAuthorized:JSON.parse(localStorage.getItem('Auth'))});

    console.log(this.state.darkMode);
    console.log(window.localStorage);
  }




  authorize=()=>{
    this.auth0.authorize();

  };

  logOut=()=>{
    this.setState({isAuthorized:false},
      ()=>(localStorage.setItem('Auth',this.state.isAuthorized)))

  };

  handleAuthentication = () =>{
    this.auth0.parseHash((err,authResult)=>{
      if(authResult&&authResult.accessToken){
        this.setState({isAuthorized:true},()=>{
          this.props.history.push('/user')
        })
        localStorage.setItem('Auth',this.state.isAuthorized)
      }else if(err){
        console.log(err)
      }
    })
  }
  render(){
    const {isAuthorized} = this.state;
    return (
      <AuthContext.Provider value={{
        isAuthorized,
        authorize:this.authorize,
        logOut:this.logOut,
        handleAuthentication:this.handleAuthentication
      }}>
        <ThemeContext.Provider value={{
          darkMode: this.state.darkMode,
          changeTheme:this.changeTheme
        }}>
          {this.props.children}
        </ThemeContext.Provider>
      </AuthContext.Provider>
    )
  }
}

export function withAuth(WrappedComponent) {
  return class AuthHOC extends Component{
    render() {
      return(
        <AuthContext.Consumer>
          {authProps=>(
            <ThemeContext.Consumer>
              {themeProps=>(
                <WrappedComponent {...authProps} {...themeProps} {...this.props}/>
              )}
            </ThemeContext.Consumer>
          )}
        </AuthContext.Consumer>
      )
    }
  }
  
}

const AuthProviderWithRouter = withRouter(Context);
export  {AuthProviderWithRouter as Context};